import React, {useState, useRef} from "react";
import books from "../Images/library.png";
import { useAuth0 } from "../react-auth0-spa";
import { Overlay } from "react-bootstrap";

function Home() {
    const { isAuthenticated, loginWithRedirect, logout } = useAuth0();
    const [show, setShow] = useState(false);
    const target = useRef(null);

    return (
      <div>
      {!isAuthenticated && (
        <span>
        <img src={books} alt="books" className="booksImg" style= {styles.img} />
        <Overlay target={target.current} show={show} placement="center">
          <button style={styles.button} onClick={() => loginWithRedirect({})}>Log in</button>
        </Overlay>
        </span>
      )}
      </div>
    );
}

export default Home;

const styles = {
  img: {
    width: "100%",
    height: "80%",
    zIndex: 1
  },
  button : {
    zIndex: -1,
    float: "center",
    position: "absolute",
    margin: "40%"
  }

};

